* {
    margin: 0;
    padding: 0;
    font-family: 'Raleway';
}

#full{
    float: left;
    padding :0;
    margin:0;
    width: 100%;
    max-height:100%;
    min-height:100vh;
    min-width: 910px;
    text-align: center;
    overflow: hidden;
}

.designer{
    width: 100%;
    height: 100vh;
}

#logo{
    float: left;
    height: 10vh;
    margin :0px;
    padding-top: 10px;
    padding-left: 15px;
    color: white;
    font-family: 'Alba';
    font-size: 40px;
    }

#headbar3{
    float: left;
    height: 90px;
    width: 100%;
    background-color: #7eab54;
    position: relative;
}

#img_cropper{
    float:right;
    width: 60px;
    height: 60px;
    margin-top: 12px;
    margin-right: 5px;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    border: 2px solid white;
}

#user_img{
    height: 60px;
    width: 60px;
}

#userid{
    float: right;
    padding: 10px;
    margin-top: 20px;
    color : white;
}

#backtoclass{
    color: white;
    float: right;
    padding: 10px;
    margin-top: 20px;
}

#menu{
    height: 100%;
    float:right;
}

#menu_button{
    height: 100%;
    width: 100%;

}

#menu-img{
    height: 60px;
    width:60px;
}

#class_container:hover {
    background-color:#bfbfbf;
}

/*----------------------*/

#makeclass{
    float: left;
    width: 100%;
    height: calc(100vh - 90px);
    align-items: center;
    justify-content: center;
    font-size: 30px;
}

#clicktext1{
    padding: 0px;
    padding-top:25vh;
    margin-bottom: 0px;
}
#clicktext2{
    padding:0px;
    margin-top: 0px;
    margin-bottom: 0px;
}

#plus{
    background-color: #7eab54;
    color: white;
    margin-top: 40px;
    height: 100px;
    width: 100px;
}



#body{
    width:100%;
    height: 5vh;
    padding-bottom: 1vh;
    padding-top : 1vh;
    position: relative;
}

#body2{
    width: 100%;
    height: calc(90vh - 90px);
    position: relative;
    scroll-behavior: smooth;
    overflow-y: scroll;
}

.center{
    display: flex;
    justify-content: center;
    align-items: center;
    width: inherit;
    height: inherit;
}

.box{
    height: 60px;
    width : 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-collapse: collapse;
}


